@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
.storage {
  position: relative;
  padding: 2rem;
  background-color: #1d2c67;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}

.storage--text {
  margin-bottom: 1rem;
  font-weight: 400;
  text-align: center;
}

.storage--text .ammount {
  font-weight: 700;
}

.storage__progress {
  position: relative;
}

.storage--range {
  margin-bottom: 1rem;
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  width: 100%;
  height: 1.6rem;
  background: #0c122c;
  outline: none;
  border-radius: 100px;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  -ms-border-radius: 100px;
  -o-border-radius: 100px;
}

.storage--progress {
  position: absolute;
  top: 0.2rem;
  left: 0.2rem;
  height: 1.2rem;
  background: #ffa399;
  background: linear-gradient(90deg, #ffa399 0%, #ff4d97 100%);
  border-radius: 100px;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  -ms-border-radius: 100px;
  -o-border-radius: 100px;
}

.storage--range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 1rem;
  height: 1rem;
  background: #ffffff;
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-border-radius: 100%;
  cursor: pointer;
}

.storage--range::-moz-range-thumb {
  width: 1rem;
  height: 1rem;
  background: #ffffff;
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-border-radius: 100%;
  cursor: pointer;
}

.storage__capacity {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 2.5rem;
}

.storage__indicator {
  position: absolute;
  bottom: -2.5rem;
  left: 0;
  right: 0;
  margin: auto;
  padding: 1rem 1.5rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: background 0.3s ease-in-out;
  -webkit-transition: background 0.3s ease-in-out;
  -moz-transition: background 0.3s ease-in-out;
  -ms-transition: background 0.3s ease-in-out;
  -o-transition: background 0.3s ease-in-out;
}

.storage__indicator--corner {
  display: none;
}

.storage__indicator--text {
  display: flex;
  align-items: center;
  color: #848794 !important;
}

.storage__indicator--text .ammount {
  margin-right: 0.5rem;
  color: #000000;
  font-weight: 700;
  font-size: 3rem;
}

.storage__indicator--text,
.storage__indicator--text .ammount {
  transition: color 0.3s ease-in-out;
  -webkit-transition: color 0.3s ease-in-out;
  -moz-transition: color 0.3s ease-in-out;
  -ms-transition: color 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out;
}

:root {
  font-size: 14px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
  color: #dddbff;
}

body {
  height: 100vh;
  padding: 2rem;
  background-color: #0c122c;
  background-image: url(/Juandamartn/fylo-data-storage/static/media/bg-mobile.3da1358c.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.options {
  display: flex;
  flex-direction: column;
  padding: 3rem;
  margin-bottom: 1rem;
  min-width: 264px;
  background-color: #1d2c67;
  border-radius: 15px 120px 15px 15px;
  -webkit-border-radius: 15px 120px 15px 15px;
  -moz-border-radius: 15px 120px 15px 15px;
  -ms-border-radius: 15px 120px 15px 15px;
  -o-border-radius: 15px 120px 15px 15px;
}

.options__logo {
  margin-bottom: 2rem;
}

.options__controls {
  display: flex;
}

.options__controls--file,
.options__controls--folder,
.options__controls--upload {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.8rem 1rem;
  margin-right: 1rem;
  background-color: #0c122c;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  cursor: pointer;
}

.options__controls--file,
.options__controls--folder,
.options__controls--upload,
.options__controls--file svg path,
.options__controls--folder svg path,
.options__controls--upload svg path {
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.options__controls--file:hover,
.options__controls--folder:hover,
.options__controls--upload:hover {
  background-color: #dddbff;
}

.options__controls--file:hover svg path,
.options__controls--folder:hover svg path,
.options__controls--upload:hover svg path {
  fill: #1d2c67;
}

@media screen and (min-width: 426px) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(/Juandamartn/fylo-data-storage/static/media/bg-desktop.178ff150.png);
    background-size: contain;
    background-position: center bottom;
  }

  .App {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
  }

  .options {
    width: 370px;
    margin-right: 2rem;
  }

  .storage {
    width: 500px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .storage--text {
    text-align: left;
  }

  .storage__capacity {
    margin-bottom: 0px;
  }

  .storage__indicator {
    top: -4rem;
    bottom: unset;
    left: unset;
    right: 2rem;
    border-radius: 10px 10px 0 10px;
    -webkit-border-radius: 10px 10px 0 10px;
    -moz-border-radius: 10px 10px 0 10px;
    -ms-border-radius: 10px 10px 0 10px;
    -o-border-radius: 10px 10px 0 10px;
  }

  .storage__indicator--corner {
    display: block;
    position: absolute;
    bottom: -1.5rem;
    right: 0;
    width: 1.5rem;
    height: 1.5rem;
    -webkit-clip-path: polygon(100% 0, 0 0, 100% 100%);
            clip-path: polygon(100% 0, 0 0, 100% 100%);
    transition: background 0.3s ease-in-out;
    -webkit-transition: background 0.3s ease-in-out;
    -moz-transition: background 0.3s ease-in-out;
    -ms-transition: background 0.3s ease-in-out;
    -o-transition: background 0.3s ease-in-out;
  }
}

@media screen and (min-width: 954px) {
  .options {
    margin-bottom: 0;
  }
}

